import { createStore } from "redux";

const divToggle = document.getElementById("divToggle");
// const divToggle = document.querySelector(".toggle");
// console.log(divToggle);
// console.dir(divToggle);
// divToggle.classList.add("toggle");
// divToggle.classList.add("a");
// divToggle.classList.add("b");
// divToggle.classList.add("activ");
// console.log(divToggle);

const counter = document.querySelector("h1");
const btnIncrease = document.querySelector("#increase");
const btnDecrease = document.querySelector("#decrease");

const TOGGLE_SWITCH = "TOGGLE_SWITCH";
const INCREASE = "INCREASE";
const DECREASE = "DECREASE";

const toggleSwitch = () => ({ type: TOGGLE_SWITCH });
const increase = (difference) => ({ type: INCREASE, difference });
const decrease = () => ({ type: DECREASE });

const initialState = {
  toggle: false,
  counter: 0,
};

//stateÍ∞Ä undefinedÏùº ÎïåÎäî initialStateÎ•º Í∏∞Î≥∏Í∞íÏúºÎ°ú ÏÇ¨Ïö©
function reducer(state = initialState, action) {
  //action.typeÏóê Îî∞Îùº Îã§ÏùÄ ÏûëÏóÖÏùÑ Ï≤òÎ¶¨Ìï®
  switch (action.type) {
    case TOGGLE_SWITCH:
      return {
        ...state, // Î∂àÎ≥ÄÏÑ± Ïú†ÏßÄÎ•º Ìï¥ Ï£ºÏñ¥Ïïº Ìï©ÎãàÎã§.
        toggle: !state.toggle,
      };
    case INCREASE:
      return {
        ...state,
        counter: state.counter + action.difference,
      };
    case DECREASE:
      return {
        ...state,
        counter: state.counter - 1,
      };
    default:
      return state;
  }
}

const store = createStore(reducer);

const render = () => {
  const state = store.getState(); // ÌòÑÏû¨ ÏÉÅÌÉúÎ•º Î∂àÎü¨ ÏòµÎãàÎã§.

  // ÌÜ†Í∏ÄÏ≤òÎ¶¨
  if (state.toggle) {
    divToggle.classList.add("activ");
    // console.log("a");
  } else {
    // console.log("b");
    divToggle.classList.remove("activ");
  }

  //Ïπ¥Ïö¥ÌÑ∞ Ï≤òÎ¶¨
  counter.innerText = state.counter;
};

render();
store.subscribe(render);

divToggle.onclick = () => {
  store.dispatch(toggleSwitch);
};

btnIncrease.onclick = () => {
  store.dispatch(increase(1));
};

btnDecrease.onclick = () => {
  store.dispatch(decrease());
};
